import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import authService from '../../../service/authService'


class Signup extends Component {
  constructor(props){
    super(props);
    this.state = { 
          username: '', 
          password: '',
          error: '' 
      }
    this.service = new authService();
  }

  handleFormSubmit = (event) => {
    event.preventDefault();
    const username = this.state.username;
    const password = this.state.password;
  
    this.service.signup(username, password)
    .then( response => {
        this.setState({
            username: "",
            password: ""
        })

        this.props.setUser(response)
        window.location.assign('/')

    })
    .catch( error => {console.log(error.response.data.message)
      this.setState ({error: error.response.data.message})
      })
    }
  
  handleChange = (event) => {  
    const {name, value} = event.target;
    this.setState({[name]: value});
  }
      
  
  render(){
    return(
      <div className="signup-container">
      <form onSubmit={this.handleFormSubmit} className="signup-form">
<h1>Bienvenido amigo!</h1>
      <div className="rowForm">
      <label>Tu nombre </label>
      <input type="text" name="username" className="signup-box" value={this.state.username} onChange={e => this.handleChange(e)} />
      </div>

      <div className="rowForm">
      <label>Tu contraseña </label>
      <input type="password" placeholder="******" name="password" className="signup-box" value={this.state.password} onChange={e => this.handleChange(e)} />
      </div>

      <input type="submit" value="Regístrate" className="btn-signup" />
      <small>¿Ya tienes una cuenta? <Link className="link" to={"/"}> Inicia sesión aqui</Link></small>
      <p className="error-message">{this.state.error}</p>

  
    </form>

      
      </div>
    )
  }
}

export default Signup

