import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import authService from '../../../service/authService'


class Login extends Component {
  constructor(props){
    super(props)
    this.state = { username: '', password: '' , error: ''}
    this.service = new authService()
  }

  handleFormSubmit = (event) => {
    event.preventDefault()
    const username = this.state.username
    const password = this.state.password

    this.service.login(username, password)
    .then( response => {
        this.setState({ 
              username: "",
              password: "" })
        this.props.setUser(response)
        window.location.assign('/')

    })
    .catch( error => {console.log(error.response.data.message)
    this.setState ({error: error.response.data.message})
    })
  }
    
  handleChange = (event) => {  
    const {name, value} = event.target
    this.setState({[name]: value})
  }
    
  render(){
    return(

 <div className="login-container">

<form onSubmit={this.handleFormSubmit} className="login-form">
<h1>Hola amigo!</h1>

<div className="rowForm">
  <label>Mi nombre</label>
  <input type="text" name="username" className="login-box" value={this.state.username} onChange={e => this.handleChange(e)} />
</div>

<div className="rowForm">
  <label>Mi contraseña</label>
  <input type="password"  name="password"placeholder="******" className="login-box" value={this.state.password} onChange={e => this.handleChange(e)} />
</div>
    
  <input type="submit" value="Inicia sesión" className="btn-login" />
  <small>¿No tienes una cuenta? <Link className="link" to={"/signup"}> Regístrate ahora</Link></small>
  <p className="error-message">{this.state.error}</p>


      </form>
      </div>

    )
  }
}

export default Login

