import React, { Component } from 'react'
import scenarioService from '../../service/scenarioService'
import ScenarioCard from './ScenarioCard'
import ScenarioForm from './ScenarioForm'
import SearchBar from '../searchbar/SearchBar'




class ScenarioLists extends Component {
constructor(props) {
        super(props)
        this.state = {
            scenarios: [],
            copyScenarios: [],
            searchString: "",
            
        }
    this.services = new scenarioService()
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ ...this.state, loggedInUser: nextProps["userInSession"] })
    }

    getAllScenarios = () => {
       return this.services.getScenarios()
            .then(data => { 
                this.setState(
                {...this.state, scenarios: data, copyScenarios:data})
            })
    }

    searchInfo = theSearch => {
        let copyScenarios = [...this.state.copyScenarios]
            console.log("hello")
            copyScenarios = this.state.scenarios.filter(scenario => 
                {return scenario.title.toUpperCase().includes(theSearch.toUpperCase()) 
                    || scenario.description.toUpperCase().includes(theSearch.toUpperCase()) 
}) 
                console.log("resultados",copyScenarios)
        this.setState({...this.state, copyScenarios: copyScenarios})
    }



    componentDidMount() {
        this.getAllScenarios()
    }


    render() {
        // console.log(this.state.scenarios)
        return (

        <div className="container scenario">

<div className = "nav-medium">
   {<SearchBar searchInfo={this.searchInfo} />}
   </div>
   
        {<ScenarioForm refreshScenarios={this.getAllScenarios} />}

        <div className="full-scenario">
        {// console.log(this.state.sceanrios)
           this.state.scenarios!== undefined && 
           (this.state.copyScenarios.map(scenario => <ScenarioCard key={scenario._id} {...scenario} />)
              ) 
                    }
                </div>
            </div>
        )
    }
}


export default ScenarioLists
