import React from 'react'
import { Link } from 'react-router-dom'

const ScenarioCard = scenario => {

    return (
        <div className="single-scenario">

        <Link className="" to={`/scenario/${scenario._id}`}>

        {/* <img className="card-img-top" src={scenario.imageUrl} alt="Card image cap"></img> */}

        <h5 className="card-title">{scenario.title}</h5>
        <p className="card-text">{scenario.description}</p>
        </Link>
        </div>
    )
}

export default ScenarioCard

