import axios from "axios"

export default class services {

    constructor() {

        console.log(process.env.REACT_APP_API_URL)

        this.service = axios.create({
            baseURL: `${process.env.REACT_APP_API_URL}/`,
            withCredentials: true
        })
    }
// GET ALL OF THE SCEANRIOS
    getScenarios = () => {
        const promise = this.service.get("getAllScenarios", { withCredentials: true })
            .then(res => {return res.data})
            .catch(err => {console.log(err)})
        return promise
    }

//GET THE DETAILS OF SCENARIO
    getOneScenario = idScenario => {
        const promise = this.service.get(`getOneScenario/${idScenario}`)
            .then(res => res.data)
            .catch(err => console.log(err))
    
        return promise
    }
//SEE THE QUESTIONS IN THE SCENARIO
    getQuestions = () => {
        const promise = this.service.get("getQuestions")
            .then(res => {return res.data})
            .catch(err => {console.log(err)})
        return promise
    }

//CREATE A NEW SCENARIO 
    postNewScenario = scenario => {
        const promise = this.service.post("postScenario", scenario)
            .then(res => {
                console.log(res);
                return res.data;
            })

        return promise
    }

// CREATE A NEW QUESTION
 createQuestion = (question, scenarioId) => {
    console.log(question, scenarioId)
    const promise = this.service.post("createQuestion", {question, scenarioId})
    .then(res => {console.log(res)
        return res.data
    })

    return promise
 }






    
    
}



