import React, {
    Component
} from 'react'
import scenarioService from '../../service/scenarioService'
import {
    Link
} from 'react-router-dom'
import Modal from 'react-modal'

import sr from '../ScrollReveal'



const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        width: '40%'
    }
}

Modal.setAppElement('#root')

class ScenarioDetails extends Component {

    constructor(props) {

        super(props)

        this.state = {
            newQuestion: [],
            scenario: {},
            question: null
        }

        this.service = new scenarioService()
        this.openModal = this.openModal.bind(this)
        this.closeModal = this.closeModal.bind(this)
    }

    componentDidMount() {
        this.service.getOneScenario(this.props.match.params.id)
            .then(response => {
                this.setState({
                    ...this.state,
                    scenario: response
                }, () => this.getQuestion())
            })

    }


    getQuestion = () => {
        const configTop = {
            delay: 10,
            duration: 3000,
            reset: true,
            scale: 1.1,
            easing: 'ease',
        }


        console.log(Math.floor(Math.random() * this.state.scenario.questions.length))
        let question = this.state.scenario.questions[Math.floor(Math.random() * this.state.scenario.questions.length)]
        console.log(question)
        this.setState({
            ...this.state,
            question: question
        }, () => {
            sr.destroy(this.refs.question)
            sr.reveal(this.refs.question, configTop)
        })
    }

    openModal = () => {
        this.setState({
            modalIsOpen: true
        });
    }

    closeModal = () => {
        this.setState({
            modalIsOpen: false
        });
    }

    handleState = e => {
        const {
            value
        } = e.target;

        this.setState({
            newQuestion: value
        })
    }


    handleSubmit = (e) => {
        e.preventDefault()
        this.service.createQuestion(this.state.newQuestion, this.state.scenario)
            .then(x => window.location.reload())

        this.setState({
            scenario: {
                newQuestion: "",
                scenario: {},
                question: null

            }
        })
        this.closeModal()
    }

    render() {

            if (!this.state.question) {
                return (

                    <div className = "scenario-details" >

                    <button onClick = {
                        this.openModal
                    }
                    className = "new-question" > Create question </button> <Modal isOpen = {
                        this.state.modalIsOpen
                    }
                    onRequestClose = {
                        this.closeModal
                    }
                    style = {
                        customStyles
                    } >

                    <div className = "create-new-question-form" >
                    <h2 > Create your questions </h2> <form onSubmit = {
                        this.handleSubmit
                    } >

                    <div className = "rowForm" >
                    <label > </label> <input type = "text"
                    className = "question-form"
                    name = "title"
                    value = {
                        this.state.newQuestion
                    }
                    onChange = {
                        (e) => this.handleState(e)
                    }
                    /> </div>

                    <button type = "submit"
                    className = "btn-add-question" > Crear </button>

                    </form> </div>

                    </Modal>     </div>

                )
            } else {
                return (

                        <div className = "scenario-details" >

                        <button className = "new-question"
                        onClick = {
                            this.openModal
                        } > < i class = "fas fa-plus" > </i> Add questions</button >

                        <Modal isOpen = {
                            this.state.modalIsOpen
                        }
                        onRequestClose = {
                            this.closeModal
                        }
                        style = {
                            customStyles
                        } >

                        <div className = "create-new-question-form" >
                        <h2 > write rAnDoM questions </h2> 
                        <form onSubmit = {
                            this.handleSubm} >

                        <div className = "rowForm" >

                        <input type = "text"
                        className = "question-form"
                        name = "title"
                        value = {
                            this.state.newQuestion
                        }
                        onChange = {
                            (e) => this.handleState(e)
                        }
                        /> </div>

                        <input type = "hidden"
                        name = "Scenario"
                        value = {
                            this.state.scenario
                        }/> <button type = "submit"
                        className = "btn-add-question" > Add </button> </form> </div></Modal>    




                        < h1 className = "h1-details" > {
                            this.state.scenario.title
                        } </h1> <h3 className = "h3-details" > {
                            this.state.scenario.description
                        } </h3>

                        <div className = "question-container" >
                        <button className = "question"
                        onClick = {
                            this.getQuestion
                        }
                        ref = "question" > {
                            this.state.question.question
                        } </button>     </div>

                        <div className = "button-container-details" >

                     
                        <Link to='/scenarios'><p className = "button-go-back">go Back</p></Link>
                        
</div>    
</div>
        )
    }
}


}

export default ScenarioDetails




