import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import authService from '../../service/authService'


class Navbar extends Component {

  constructor(props){
    super(props)
    this.state = { loggedInUser: null }
    this.service = new authService()
    this.props = props
  }


  componentDidMount = () => {
    this.setState({...this.state, loggedInUser: this.props.userInSession})
}

  componentWillReceiveProps(nextProps) {
    this.setState({...this.state, loggedInUser: nextProps["userInSession"]})
  }

  

  logoutUser = () => {
    this.service.logout()
        .then(() => {
            this.setState({ loggedInUser: null });
            this.props.setUser(null);
            this.props.closeModal()
        })
}
    
  render(){
            


    console.log(this.props)
    if(this.state.loggedInUser){

      return(

  <section className="nav-style">
    <div className="nav-left">

        <p><small className="welcome-h1"> Hola<span className="welcome-h1"> {this.state.loggedInUser.username}!</span></small></p>
      
    </div>     

   

    <div className="nav-right">

      <Link to='/scenarios'>Questions</Link>
      <Link to='/'>Home</Link>
      <Link to='/'>About</Link>
      <Link to='/scenarios'><a href="#loggout" onClick={() => this.logoutUser()}>Logout</a></Link>


    </div>
  </section>
      )
    } 
    
    else {    
      
      return (
        <section className="nav-style">
        <div className="nav-left">

        <li className="login-btn" onClick={this.props.showLogin} >Login</li>
        <li className="signup-btn" onClick={this.props.showSignup}>Signup</li>
 
        </div>   

        <div className="nav-right">
        

        <li><Link to='/scenarios'>Questions</Link></li> 
        <li><Link to='/'>Home</Link></li> 
        <Link to='/about'>About</Link>

        </div>
        </section>
            )
          }
        }
      }

export default Navbar


//componentWillReceiveProps method called before a component does anything with the new props
//method is called with the new props passed as an argumentaccess to the next set of props and the present ones. 
//we can compare the present props with the new ones and check if anything has really changed.

