import React, { Component } from 'react'
import scenarioService from '../../service/scenarioService'
import Modal from 'react-modal'


const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        width: '40%'
    }
}

Modal.setAppElement('#root')


class ScenarioForm extends Component {

    constructor(props) {

        super(props)

        this.state = {
            modalIsOpen: false,
            scenario: {
                title: '',
                description: '',
                imageUrl: '',
            }
        }

        this.services = new scenarioService()
        this.openModal = this.openModal.bind(this)
        this.closeModal = this.closeModal.bind(this)
    }

    openModal = () => {
        this.setState({ modalIsOpen: true });
    }

    showLogin = () => {
        this.setState ({modelIsOpen: true, showLogin:false})
    }

    closeModal = () => {
        this.setState({ modalIsOpen: false });
    }


    handleState = e => {
        const { name, value } = e.target;

        this.setState({
            scenario: {
                ...this.state.scenario, [name]: value
            }
        })
    }

    handleSubmit = e => {
        e.preventDefault()

        this.services.postNewScenario(this.state.scenario)
            .then(x => this.props.refreshScenarios())

        this.setState({
            scenario: {
                title: '',
                description: '',
                imageUrl: '',
            }
        })
        this.closeModal()
    }


    render() {
        return (
            <div classname= "signup-container">
                <button onClick={this.openModal} className="button-new-situation">Create a situation</button>
                <Modal isOpen={this.state.modalIsOpen} onRequestClose={this.closeModal} style={customStyles}>

                    
                    <form onSubmit={this.handleSubmit} className="create-scenario-form" >
                    <h2>Create a random situation </h2>
                    <div className="rowForm">
                            <label>Title</label>
                            <input type="text" className="form-control" name="title" value={this.state.scenario.title} onChange={(e) => this.handleState(e)} />
                        </div>

                        <div className="rowForm">
                            <label>Description</label>
                            <textarea type="text" className="form-control" name="description" value={this.state.scenario.description} onChange={(e) => this.handleState(e)} />
                        </div>

                        
                            

                        <button type="submit" className="btn-create-scenario">Crear</button>

                    </form>

                </Modal>
            </div>
        )
    }
}


export default ScenarioForm












