import React, { Component } from 'react'
import './App.css'

// Components
import SignUp from './components/auth/signup/SignUp'
import LogIn from './components/auth/login/LogIn'
import Navbar from './components/navbar/Navbar'
import ScenarioLists from "./components/scenario/ScenarioLists"
import ScenarioDetails from './components/scenario/ScenarioDetails'
import Home from './components/home/Home'
import About from './components/home/About'
// import CarouselPage from './components/home/CarouselPage'

//Service
import authService from './service/authService'

// Routing & DOM
import { Switch, Route } from 'react-router-dom'
import Modal from 'react-modal'


const customStyles = {
	content: {
			top: '50%',
			left: '50%',
			right: 'auto',
			bottom: 'auto',
			marginRight: '-50%',
			transform: 'translate(-50%, -50%)',
			width: '40%'
	}
}

Modal.setAppElement('#root')

class App extends Component {
  constructor(props){
    super(props)
    this.state = { 
      loggedInUser: null,
      modalIsOpen: false, 
      showLogin: false,
      searchString: ''
      }
    this.service = new authService()
    this.openModal = this.openModal.bind(this)
    this.closeModal = this.closeModal.bind(this)
  }

  fetchUser(){
    if( this.state.loggedInUser === null ){
      this.service.loggedin()
      .then(response =>{this.setState({loggedInUser:  response})})
      .catch( err =>{this.setState({loggedInUser:  false})})
      }
    }

    searchInfo = theSearch => {
      this.setState({
        searchString: theSearch
      },()=>console.log(this.state.searchString) )
    }
  
    setTheUser= (userObj) => {
      this.setState({loggedInUser: userObj})
    }

    openModal = () => {
		  this.setState({ modalIsOpen: true, showLogin: false });
    }

    showLogin = () => {
	    this.setState({ modalIsOpen: true, showLogin: true });
    }

    closeModal = () => {
		  this.setState({ modalIsOpen: false });
    }


    handleSubmit = e => {
      e.preventDefault()
      this.closeModal()
    }

  render() {
    this.fetchUser()

    if(!this.state.loggedInUser){
    return (
      <main>

  <Navbar userInSession={this.state.loggedInUser} showSignup={this.openModal} showLogin={this.showLogin}  searchInfo={this.searchInfo}/>
          
  <Modal isOpen={this.state.modalIsOpen} onRequestClose={this.closeModal} style={customStyles}>
  {this.state.showLogin ? <LogIn setUser={this.setTheUser} /> : <SignUp setUser={this.setTheUser} />}
	</Modal>
            <Switch>
              <Route exact path='/signup' render={() => <SignUp setUser={this.setTheUser}/>}/>
              <Route exact path='/login' render={() => <LogIn setUser={this.setTheUser} />} />
              <Route path="/scenarios" exact render={() => <ScenarioLists />} />
              <Route path="/scenario/:id" exact component={ScenarioDetails} />
              <Route exact path="/"  render={() => <Home/>} />
              <Route exact path="/about"  render={() => <About/>} />


            </Switch>


      </main> 
    )

  } else {
        return (
          <main>
            <div className="main-container">
          <Navbar userInSession={this.state.loggedInUser} setUser={this.setTheUser} closeModal={this.closeModal} />
          <Switch>
              <Route exact path="/scenarios"  render={() => <ScenarioLists/>} />
              <Route exact path="/scenario/:id"  component={ScenarioDetails} />
              <Route exact path="/"  render={() => <Home/>} />


          </Switch>
          </div>
          </main>   
        ) 
      }
    } 
  }
export default App


